@-webkit-keyframes App__header_link{
  0% {  width: 0%;
    background: rgb(178, 164, 34);
  border-radius: 50px;}
  20%{
       background: rgb(178, 34, 34);
  }
    50%{
       background: rgb(34, 178, 41);}
  100% {width: 100%;
    background: rgb(48, 34, 178);
    border-radius: 50px;}
  }

@keyframes App__header_link{
